.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-size: calc(10px + 2vmin);
    color: white;
}

.game-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.player-icon {
    margin: 1em 1em 1em 1em;
    text-align: center;
}

.card-names {
    margin: 1em 1em 1em 1em;
    text-align: center;
    font-size: 0.8em;
}

.local-player-icon {
    margin: 1em 1em 1em 1em;
    text-align: center;
    border: double white;
}

input[type=text] {
    height: calc(10px + 2vmin);
    width: calc(165px + 2vmin);
    padding-left: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 10px;
    background: none;
    text-align: center;
    font-size: 20px;
    font-weight: 0;
    font-family: 'Poppins', Arial, sans-serif;
}

input[type=submit] {
    border: none;
    width: 100%;
    background: #00abc6;
    color: #fff;
    font-family: 'Poppins', Arial, sans-serif;
    font-weight: 0;
    font-size: 0.9em;
    padding: 0.5em 0.5em;
    border-radius: 10px;
    margin: 0.7em 0 -1em 0;
}

input[type=submit]:hover {
    background: #006E7F;
}

#centered {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
}

#rules-box {
    margin: 10px;
    position: absolute;
    background: #eeeeee;
    top: 5%;
    padding: 30px 20px 5px 20px;
    min-height: 10vh;
    height: 80%;
    width: 60%;
    color: #282c34;
    font-size: 0.7em;
    text-align: left;
    overflow-y: scroll;
    border-radius: 10px;
    z-index: 1;
}

#rules-button {
    flex: 1 1;
    border: none;
    padding: 0.5em;
    background: #fff;
    color: #282c34;
    margin-top: 15px;
    margin-right: 10px;
    border-radius: 10px;
    position: absolute;
    right: 1em;
    font-family: 'Poppins', Arial, sans-serif;
    font-size: 0.5em;
    cursor: pointer;
}

#rules-button:hover {
    background: #999;
}

.App-link {
    color: #61dafb;
}

#event-log {
    position: fixed;
    text-align: left;
    left: 1ch;
    font-size: 0.6em;
}

#back {
    padding: 0 0 0 0;
    align-self: baseline;
    margin-top: -20px;
    margin-left: -10px;
    margin-bottom: 1em;
    float: left;
    background: none;
    border: none;
    cursor: pointer;
}

#footer {
    text-align: center;
    font-size: 1em;
    position: fixed;
    bottom: 30px;
    color: white;
    left: 0;
    right: 0;
}

.game-button {
    background: none;
    border: white;
    border-radius: 10px;
    color: #fff;
    font-family: 'Alata', Arial, sans-serif;
    font-size: 0.7em;
    cursor: pointer;
}

.form-table {
    background-color: #eeeeee;
    color: #282c34;
    padding: 30px 20px 30px 20px;
    text-align: center;
    width: 300px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
}

#copy-button {
    height: 40px;
    width: 40px;
    padding: 5px -2px 5px -2px;
}

#create-name {
    font-size: 20px;
    font-weight: bold;
    word-wrap: break-word;
    margin: 0 0 0.6em;
    font-family: 'Poppins', Arial, sans-serif;
}

#create-code {
    font-size: 20px;
    font-weight: bold;
    word-wrap: break-word;
    margin: 0.8em 0 0;
    font-family: 'Poppins', Arial, sans-serif;
}

.buttons {
    width: 15%;
    min-width: 15ch;
    table-layout: fixed;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
}

.buttons tr {
    display: flex;
}

.buttons tr td {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

#header {
    font-family: 'Alata', Arial, sans-serif;
    /*padding: 0 0 20vh 0;*/
}

h1 {
    margin-bottom: -0.9em;
}

.form-button {
    flex: 1 1;
    border: none;
    padding: 0.5em;
    background: #00abc6;
    color: #fff;
    margin: 5px;
    font-family: 'Poppins', Arial, sans-serif;
    font-weight: 0;
    font-size: 0.9em;
    border-radius: 10px;
}

.form-button:hover {
    /* background: #61dafb; */
    background: #006E7F;
}

a {
    color: white;
}

#captain-color {
    color: #FF863A;
}

#assassin-color {
    color: #FF595E;
}

#duke-color {
    color: #8AC926
}

#ambassador-color {
    color: #1982C4;
}

#contessa-color {
    color: #6A4C93;
}


